.lineup {
    background-color: $rosa;
    padding: $separacion 0;
    
    h3, p {
        text-align: center;
        color: $blanco;
    }
    .dia {
        text-transform: uppercase;
        margin: $separacion 0;
    }
}

.escenarios-contenedor {
    @include tablet {
        @include grid(2, $separacion);
    }

    .escenario {
        padding-top: $separacion / 2; //en SASS podemos dividir 
    }
    .nombre-escenario {
        color: $negro;
        font-size: 2rem;
        font-weight: $delgada;

        &:after { //el amperson es para que aparesce pegado en .nombre-escenario:after. Esto es un pseudoelemento
            content: ''; //si no tiene el content no se ve
            width: 20rem;
            height: 10rem;
            display: block;
            //background-color: $morado;
            margin: 2rem auto 0 auto; //de esta forma va a quedar centrado
            background-size: 15rem;
            background-repeat: no-repeat;
            background-position: center center; //alinea horizontalmente, alinea verticalmente
        }
    }


}

.edm .nombre-escenario {
    &:after {
        background-image: url(../img/icono_edm.png);
    }
}


.rock .nombre-escenario {
    &:after {
        background-image: url(../img/icono_rock.png);
    }
}

.calendario {
    padding: 0;
    list-style: none;
    li {
        padding: $separacion / 2;
        font-weight: $bold;

        span {
            font-weight: $regular;
        }
    }
}

.bg-verde .calendario li:nth-child(odd) { /*para que vaya de uno por medio en la lista, tambien se representa con la formula: 2n+1*/
    background-color: darken($verde, 10); //color verde a una intensidad de 10, darken es una funcion exclusiva de SASS, que le agrega negro al color, va del 1 al 100
    //tambien esta lighten que aplica color blanco al color
}

.bg-amarillo .calendario li:nth-child(odd) { /*para que vaya de uno por medio en la lista, tambien se representa con la formula: 2n+1*/
    background-color: darken($amarillo, 10); //color verde a una intensidad de 10, darken es una funcion exclusiva de SASS, que le agrega negro al color, va del 1 al 100
    //tambien esta lighten que aplica color blanco al color
}