.sobre-festival {
    padding: $separacion 0; //5rem y 0 izquierda y derecha.

    @include tablet { //mixin tablet, lo usamos porque si no en la version compilada se repetian los selectores
        //display: grid;
        //grid-template-columns: 1fr 1fr;
        @include grid(2, $separacion); //columnas, y separacion entre ellas.
    }
}

.contenido-festival {
    display: flex;
    flex-direction: column;
    justify-content: center; //alinea verticalmente, se usa asi porque esta en modo columna

    h2 {
        font-size: 3rem;
        
    }
    .fecha {
        color: $rosa;
        font-weight: $bold;
    }
    .texto {
        line-height: 1.5;
    }
}