.video {
    position: relative; //la posicion relativa es para que el div y el texto de abajo con posiscion absoluta, no se salga de los limites del video o algo asi, hay nota en udemy del video
    max-height: 70rem; //si pueden ser menos pero nunca mas de  eso
    overflow: hidden; /*Todo lo que no quepa del video se oculta*/

    .overlay {
       position: absolute;
        width: 100%;
        height: 100%;
        //background: linear-gradient( to right, $rosa, $amarillo); //Hacia donde va el degradado, despues del color, puede ir porcentaje de adonde empieza el degradado
        
        //Ahora utilizando la herramienta para hacer degradados mas faciles con colorzilla
        /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#891c1e+0,9e37f2+67,ffffff+100&0.8+0,0.5+100 */
        background: -moz-linear-gradient(-45deg,  rgba(137,28,30,0.8) 0%, rgba(158,55,242,0.6) 67%, rgba(255,255,255,0.5) 100%); /* FF3.6-15 */
        background: -webkit-linear-gradient(-45deg,  rgba(137,28,30,0.8) 0%,rgba(158,55,242,0.6) 67%,rgba(255,255,255,0.5) 100%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(135deg,  rgba(137,28,30,0.8) 0%,rgba(158,55,242,0.6) 67%,rgba(255,255,255,0.5) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#cc891c1e', endColorstr='#80ffffff',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */


    }

    video { /*todos los videos por defecto tiene un display inline */
        display: block;
        width: 100%;
    }

    .contenido-video { //se pone aqui para que el anidamiento no se convierta en un problema en el futuro por ser muy largo el selector
        display: flex;
        align-items: center; //centra verticalmente
        height: 100%;
        position: relative;//para que con el z-index podamos seleccionar el textos
        z-index: 1;

        h2 {
            display: flex;
            flex-direction: column; //el h2 funciona como un elemento, el span como otro y por eso hace el salto de linea.
            align-items: flex-start; //forzar a que los elemento se vayan hacia la izquierda, para que el span no se extienda al tama;o del h2.
            color: $blanco;
            font-size: 7rem;
            span {
                background-color: $rosa;
                font-size: 2rem;
                padding: 1rem 2rem; //un rem arriba y abajo, 2 izquierda y derecha.
            }
        }
    }
}