.galeria {
    padding: $separacion 0;

    H3 {
        text-align: center;
    }
}

.galeria-imagenes {
    padding: 0; //TODOS LOS ORDER LIST TIENEN PADDING
    list-style: none;
    margin-top: $separacion;

    @include grid(2, $separacion); 

    @include tablet {
        @include grid(4, $separacion / 2);
    }

    @include desktop {
        @include grid(6, $separacion / 2 );
    }
}

.overlay {
    position: fixed;/*posicion fija de la imagen*/
    background-color: rgba(0,0,0,.5);
    width: 100%;
    height: 100%;
    top: 0;
    display: grid;
    place-items: center; /*CENTRA TANTO VERTICAL COMO HORIZONTALMENTE*/
}

.btn-cerrar {
    width: 5rem;
    height: 5rem;
    background-color: $blanco;
    display: grid;
    place-items: center;
    font-weight: $black;
    font-size: 3rem;
    border-radius: 50%;

    &:hover {
        cursor: pointer;
    }
}