//En SASS puedes anidar tu codigo

.header {
    background-color: $verde;

    .contenido-header { //como es hijo del header podemos colocarlo de esta forma
    //    padding: calc( 5rem - 3rem ) 0;        
       padding: $separacion / 2 0; //todas las operacion tal cual lo harias en javaScript estan soportadas        
      
      @include tablet {
         display: flex;//mueve un elemento a la izquierda y otro a la derecha
         justify-content: space-between; 
         //Para alinear verticalmente en flex box es align-items
         align-items: center;   
      }
    
    }

    h1 {
        color: $blanco;
        margin-bottom: 1rem;
        text-align: center;
        @include tablet {
            margin-bottom: 0;
        }
        //@include nuevoMixin(uppercase, 3rem); //Un mixin es algo que puedes reutilizar
    }

    &.fijo {
        position: fixed;  /*Para que sirva que se queda pagado a la pantalla*/
        width: 100%;
        box-shadow: 0 .3rem .4rem #444;

    }

}


.navegacion-principal { //lo cortamos por fuera para que el selector de app.css sea mas pequeno
    display: flex;
    gap: 2rem; //separacion entre los links
    //si lo hicieramos con grid quedaria asi â†’
    // display: grid;
    // grid-template-columns: repeat(3, 1fr);
    // column-gap: 2rem;
    justify-content: center; //Lo centra horizontalmente
    a {
       color: $blanco;
       font-size: 2rem;
       //@include nuevoMixin(lowercase, 2rem);
       &:hover { //Este amperson hace referencia al elemento padre. Con a da error en el compilado de css al repetir sin sentido una a
        color: $amarillo;
        }
    }


  }