html {
    font-size: 62.5%; //Esto nos daba un tamano de 10px  = 1 rem
    box-sizing: border-box;
}
*, *:before, *::after {
    box-sizing: inherit;
}
body {
    font-family: $fuentePrincipal;
    font-size:  1.6rem; //16 pixeles

    &.fijar-body {
        overflow: hidden; /*YA NO PUEDO DAR SCROLL*/
    }
}
p {
    font-size: 2rem;
    color: $negro;
}
.contenedor {
    max-width: 120rem; //1200 px
    width: 95%; //esto para que cuando el tamano sea menor a 1200px aplican un width del 95%
    margin:  0 auto; //se centra el contenido.
}
* {
    text-decoration: none;
}
img {
    max-width: 100%; //imagenes responsivas
}
h1, h2, h3 {
    margin: 0; //porque todos los heading tienen margenes por defecto
}

h1 {
    font-size: 4rem;
    // font-weight: $black;
}
h2 {
    font-size: 3.6rem;
} 
h3 {
    font-size: 3rem;
}